@media (min-width: 1088px) {
  body#development main {
    max-width: 960px;
    width: 960px;
  }
}
@media (min-width: 1322px) {
  body#development main {
    max-width: 1194px;
    width: 1194px;
  }
}
body#development main > div {
  width: 100%;
  position: relative;
}
body#development main > div > div.container.has_sidebar {
  width: calc(100% - 0px);
}
body#development .columns {
  display: flex;
  margin-left: -1rem;
  margin-right: -1rem;
}
body#development section.columns {
  width: 100%;
}
body#development .column {
  padding: 1rem;
}
body#development .column.is-2 {
  flex: none;
  width: calc(16.66% - 1rem);
}
body#development .column.is-3 {
  flex: none;
  width: 25%;
}
body#development .column.is-6 {
  flex: none;
  width: 50%;
}
body#development .column.is-9 {
  flex: none;
  width: 75%;
}